/* -----------------------------------------
   TYPE STYLES - /source/scss/cagov/type.scss
----------------------------------------- */



body {
  font: #{$font-size-regular}/#{$line-height-base} $font-family-sans-serif;
  color: var(--gray-900, #3b3a48);
  overflow-y: scroll;

  @include media-breakpoint-up(sm) {
    font-size: calc(#{$font-size-regular} + #{$ratio});
  }
}

a {
  color: var(--color-p2, #046b99);

  &:hover,
  &:focus {
    color: var(--color-p2-darker, #214a68);
  }

  &:focus {
    @include outline(2px);
  }
}

.featured-narrative {
  border-bottom: 1px solid #d6d6d6;
  border-top: 1px solid #d6d6d6;
  padding: 10px 10px 0;
  margin-bottom: calc($line-height-computed / 2);
  margin-left: 20px;
}

// TYPOGRAPHY
// --------------------------------------------------

// Responsive Font Sizes
p {
  margin-bottom: 0.7rem;

  @include media-breakpoint-up(sm) {
    margin-bottom: calc(0.7rem + #{$ratio});
  }
}

.lead {
  font-size: $font-size-large;
  margin-bottom: 1rem;
  font-weight: $font-weight-5;

  @include media-breakpoint-up(sm) {
    font-size: calc(#{$font-size-large} + #{$ratio});
    margin-bottom: calc(1rem + #{$ratio});
  }
}

.small {
  font-size: $font-size-base;
  margin-bottom: 0.65rem;

  @include media-breakpoint-up(sm) {
    font-size: calc(#{$font-size-base} + #{$ratio});
    margin-bottom: calc(0.65rem + #{$ratio});
  }
}

.btn {
  font-size: $font-size-base;

  @include media-breakpoint-up(sm) {
    font-size: calc(#{$font-size-base} + #{$ratio});
  }
}

.btn-lg {
  font-size: 1.125rem;

  @include media-breakpoint-up(sm) {
    font-size: calc(#{$font-size-regular} + #{$ratio});
  }
}

.btn-sm {
  font-size: 0.9rem;

  @include media-breakpoint-up(sm) {
    font-size: calc(0.9rem + #{$ratio});
  }
}

// Responsive Font Sizes
h1,
.h1 {
  // color: #c60; // Orange
  span[class^="ca-gov-icon-"]:first-child {
    display: none; // hide icon used in v4
  }

  // @include font-size(2.4);
  font-size: $font-size-h1;
  margin-top: 3rem;
  margin-bottom: 1.6rem;
  font-weight: $font-weight-7;
  line-height: $font-lineheight-1;

  @include media-breakpoint-up(sm) {
    font-size: calc(#{$font-size-h1} + #{$ratio-headers});
    margin-top: calc(3rem + #{$ratio-headers});
    margin-bottom: calc(1.6rem + #{$ratio-headers});
  }

  font-family: $headings-font-family;
}

h2,
.h2 {
  // @include font-size(1.94);
  font-size: $font-size-h2;
  margin-top: 2.7rem;
  margin-bottom: 1.4rem;
  font-weight: $font-weight-7;
  line-height: $font-lineheight-2;

  @include media-breakpoint-up(sm) {
    font-size: calc(#{$font-size-h2} + #{$ratio-headers});
    margin-top: calc(2.7rem + #{$ratio-headers});
    margin-bottom: calc(1.4rem + #{$ratio-headers});
  }

  font-family: $headings-font-family;
}

h3,
.h3 {
  // @include font-size(1.54);
  font-size: $font-size-h3;
  margin-top: 2.4rem;
  margin-bottom: 1.2rem;
  font-weight: $font-weight-7;
  line-height: $font-lineheight-3;

  @include media-breakpoint-up(sm) {
    font-size: calc(#{$font-size-h3} + #{$ratio-headers});
    margin-top: calc(2.4rem + #{$ratio-headers});
    margin-bottom: calc(1.2rem + #{$ratio-headers});
  }

  font-family: $headings-font-family;
}

h4,
.h4 {
  // @include font-size(1.25);
  font-size: $font-size-h4;
  margin-top: 2rem;
  margin-bottom: 1rem;
  font-weight: $font-weight-7;
  line-height: $font-lineheight-4;

  @include media-breakpoint-up(sm) {
    font-size: calc(#{$font-size-h4} + #{$ratio-headers});
    margin-top: calc(2rem + #{$ratio-headers});
    margin-bottom: calc(1rem + #{$ratio-headers});
  }

  font-family: $headings-font-family;
}

h5,
.h5 {
  // @include font-size(1);
  font-size: $font-size-h5;
  margin-top: 1.7rem;
  margin-bottom: 0.8rem;
  font-weight: $font-weight-7;
  line-height: $font-lineheight-5;

  @include media-breakpoint-up(sm) {
    font-size: calc(#{$font-size-h5} + #{$ratio-headers});
    margin-top: calc(1.7rem + #{$ratio-headers});
    margin-bottom: calc(0.8rem + #{$ratio-headers});
  }

  font-family: $headings-font-family;
}

h6,
.h6 {
  // @include font-size(0.8);
  font-size: $font-size-h6;
  margin-top: 1.4rem;
  margin-bottom: 0.6rem;
  font-weight: $font-weight-7;
  line-height: $font-lineheight-5;

  @include media-breakpoint-up(sm) {
    font-size: calc(#{$font-size-h6} + #{$ratio-headers});
    margin-top: calc(1.4rem + #{$ratio-headers});
    margin-bottom: calc(0.6rem + #{$ratio-headers});
  }

  font-family: $headings-font-family;
}

.ie9 p {
  + h1,
  + h2,
  + h3,
  + h4 {
    padding-top: 10px; // Add top padding back into ie9's headers
  }
}

.subtitle {
  font-size: $font-size-large;
  margin-bottom: 3rem;
  font-weight: 600;
  color: var(--gray-700, #5e5e6a);

  @include media-breakpoint-up(sm) {
    font-size: calc(#{$font-size-large} + #{$ratio});
    margin-bottom: calc(3rem + #{$ratio});
  }
}

// ICON SIZING
[class*="icon-"] {
  &.big {
    @include font-size(1.94);
  }

  &.bigger {
    @include font-size(2.4);
  }

  &.biggest {
    @include font-size(3.5);
  }
}

.serif {
  font-family: $font-family-serif !important;
}

.monospace {
  font-family: $font-family-monospace !important;
}

.heading {
  font-family: $headings-font-family !important;
}

code {
  color: $code-color;
}

// pre-wrap class
.wrap {
  white-space: normal !important;
}

// LINKS NEED VISUAL INDICATORS OTHER THAN COLOR (USING UNDERLINE MIXING)
.main-content {
  @include underline($link-color, $link-hover-color);
}

/* external link */

/* External link icon */
.external-link-icon {
  display: inline-block;
  padding: 0;
  position: relative;
  text-decoration: none;

  &::after {
    content: "\e9ed";
    font-family: CaGov !important;
    font-size: 75%;
    margin-left: 3px;
  }
}

/* external link exeptions */
img ~,
svg ~,
.cagov-card,
.wp-block-button__link,
*[class*="ca-gov-icon-"],
[class*="ca-gov-icon-"] ~,
.pdf-link-icon ~,
.footer-social-links a {
  .external-link-icon {
    display: none;
  }
}

.icon-abs-right .external-link-icon {
  position: absolute;
  right: 1.5rem;
}

// UNDELINE EXEPTIONS
.panel-heading a,
.social a,
.socialsharer-container a,
.thumbnail a,
.explore-invite a,
.service-tile-content .panel-heading a,
.accordion .panel-heading a,
.tab-group .nav-tabs li > a,
.main-content .btn,
.panel-title,
.btn-more,
.accordion .panel-heading .panel-title a,
.panel-group .panel-heading .panel-title a,
.service-tile-content .panel-heading .panel-title a,
.icon-link,
.main-content a[role="button"],
.main-content a[role="tab"],
.gallery-item,
.news-link,
.news-icon {
  text-decoration: none !important;
  background-image: none !important;
}

/* Solution for links that are behind fixed Header */
#main-content a,
#main-content button {
  scroll-margin-top: 140px !important;
}

.no-underline {
  text-decoration: none !important;
  background-image: none !important;
}

// group lists (sidebar navigation)
.list-group {
  padding-left: 0 !important;
  margin-bottom: 0 !important;
}

.list-group-item {
  padding: ($spacer * 0.75) ($spacer * 1.25) ($spacer * 0.75) ($spacer * 1.25) !important;
  margin-top: 0 !important;
  background-image: none !important;
  text-decoration: none !important;
}

a.list-group-item {
  margin-bottom: 0 !important;
}

.tab.list-group-item {
  margin-bottom: -1px !important;
}

.text-highlight,
.text-highlight-primary,
.text-highlight-warning,
.text-highlight-dark {
  display: inline-block;
  padding: 0.35em 0.65em;
  font-weight: 600;
  line-height: 1;
  font-size: 95%;
  color: var(--black, #000);
  text-align: center;
  white-space: nowrap;
  vertical-align: baseline;
}
