/* -----------------------------------------
   MEDIA
   /source/scss/cagov/media.scss
----------------------------------------- */

// Figures and Captions
// --------------------------------------------------

figure {
  border: 1px solid #ddd;
  margin-bottom: 15px;
  background-color: var(--white, #fff);
  border-color: var(--gray-200, #d4d4d7);

  img {
    width: 100%;
  }
}

figcaption {
  @include font-size(0.8);

  padding: 10px;
}

// Generic image floating tags for use in content display
// --------------------------------------------------

.img-left {
  @include float-left;

  margin-right: calc($grid-gutter-width / 2);
  margin-top: 4px; // Aligns image with top of text
}

.img-right {
  @include float-right;

  margin-left: calc($grid-gutter-width / 2);
  margin-top: 4px; // Aligns image with top of text
}

// old bootstrap 3 classes
.img-circle {
  border-radius: 50%;
}

.img-rounded {
  border-radius: 5px;
}

// Extension for media object
// --------------------------------------------------

.media {
  .media-top {
    @include text-center;

    img,
    span {
      display: inherit;
      margin-bottom: $line-height-computed;
    }
  }
}

// Responsive image overide
// Inline block allows images to be centered using text-align
// --------------------------------------------------

.img-responsive {
  @include img-fluid;

  display: inline-block;
}

.img-fluid {
  display: inline-block;
}

// Progress bar //
.progress {
  font-size: 1rem;
  height: unset;
  min-height: 1.25rem;
  border-radius: $border-radius-base;
}
