/* -----------------------------------------
   YouTube Video Player
   /source/scss/cagov/youtube.scss
----------------------------------------- */

.video-player {
  position: relative;
  padding-bottom: 56.25%;
  padding-top: 30px;
  height: 0;
  overflow: hidden;
}

.video-player iframe,
.video-player object,
.video-player embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
