/* -----------------------------------------
   COURSES - /source/scss/cagov/courses.scss
----------------------------------------- */

.course-item {
  @include card;

  .thumbnail ~ .header,
  .thumbnail ~ .body {
    margin-left: calc(
      calc($grid-gutter-width / 2) + 4.375rem
    ); // 70 is the width of the thumbnail in pixels
  }

  .datetime {
    clear: both;
  }
}
